.score {
  background: #2FA6D4;
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 2px;
  border: 20px solid white;
  height: 270px;
  margin: 1rem;
  position: relative;
  width: 300px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.score h1 {
  font-size: 40px;
}
.score h2 {
  font-size: 30px;
}
.score p {
	color: white;
	font-size: 20px;
}



